<template>
  <div id="app">

    <el-container>
      <el-aside width="240px">
        <navmenu></navmenu>
      </el-aside>
      <el-container>
        <el-header>
          <router-view name="header"></router-view>
        </el-header>
        <el-main>
          <router-view name="main"></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import NavMenu from '@/components/NavMenu'

export default {
  name: 'app',
  components: {
    'navmenu': NavMenu
  },
}

</script>

<style>
::-webkit-scrollbar {display:none}
html {
  height: 100%;
}
body {
  margin: 0px;
  height: 100%;
}
.el-container {
  height: 100%;
}
.el-header {
  background-color: #f1f1f1;
  height: 120px !important;
  padding-left: 30px;
  padding-right: 30px;
}
.el-main {
  background-color: #f1f1f1;
  padding-right: 30px;
  padding-left: 30px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%
}
</style>



