<template>
  <el-row class="tac">
    <el-col :span="24">
            <!-- active-text-color="#ffd04b" -->
      <el-menu :default-active="activeNav" class="el-menu" @open="handleOpen" @close="handleClose" 
      unique-opened
      router
      background-color="#00577E"
      text-color="#fff" 
      active-text-color="#fff"
      >
      	<div id="div_logo">
      		<a href=""><img src="@/assets/icon/icon_text.png" id="icon_text"></a>
      		<a href=""><img src="@/assets/icon/icon_more.png" id="icon_more"></a>
      	</div>
      	<el-menu-item index="/Overview">
        <span slot="title" class="nav_span"><b>Overview</b></span>
      	</el-menu-item>
      	<div id="div_client">
      		<span id="clients">CLIENTS</span>
      		<a @click="open"><img src="@/assets/icon/icon_plus.png" id="icon_plus"></a>
      		<a href=""><img src="@/assets/icon/icon_search.png" id="icon_search"></a>
      	</div>
        <el-submenu v-for="item in menu" :index="item.id" :key="item.id">
        <template slot="title">
          <span v-text="item.name" class="nav_span"></span>
        </template>
        <el-menu-item-group v-for="sub in item.sub" :key="sub.componentName">
          <el-menu-item class="nav-name" :index="sub.componentName" v-text="sub.name">
          </el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      </el-menu>
    </el-col>
  </el-row>
</template>

<script>
  import menu from '@/config/menu-config.js'
  export default {
    data () {
      return {
        menu: menu,
        activeNav: '/Overview'
      }
    },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      open() {
        this.$prompt('ADD CLIENT', {
          confirmButtonText: 'DONE',
          cancelButtonText: 'CANCEL',
          inputPattern: /[A-Za-z]/,
          inputErrorMessage: 'Wrong Name Format'
        }).then(({ value }) => {
          this.$message({
            type: 'success',
            message: 'Add Client Successed'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Input Canceled'
          });       
        });
      }
    },
    watch: {
      '$route' (to, from) {
        this.activeNav = '/' + this.$route.path.split('/')[1]
      }
    }
  }
</script>

<style>
.el-menu span {
	display: inline-block;
	line-height: 48px;
	vertical-align: middle;

}
	.el-row {
		height: 100%;
	}
	.el-col {
		height: 100%;
	}
	.el-menu {
		height: 100%;
		letter-spacing: 1px;
	}
	.nav_span {
		margin-left: 25px;
		font-weight: bold;
	}
	.nav-name {
		padding-left: 60px !important;
	}
	#div_logo {
		margin-top: 100px;
		margin-bottom: 10px;
	}
	#icon_text {
		margin-left: 40px;
		height: 40px;
	}
	#icon_more {
		float: right;
		height: 18px;
		margin-top: 12px;
		margin-right: 24px;
	}
	#div_client {
		margin-top: 10px;
	}
	#clients {
		color: #fff;
		font-size: 18px;
		font-weight: bold;
		margin-left: 42px;
		margin-bottom: 10px;
	}
	#icon_plus {
		height: 18px;
		margin-left: 46px;
    cursor: pointer;
	}
	#icon_search {
		height: 18px;
		margin-left: 8px;

	}
  /*菜单关闭*/
.el-submenu>.el-submenu__title .el-submenu__icon-arrow{
  -webkit-transform: rotateZ(-90deg); 
  -ms-transform: rotate(-90deg);
  transform: rotateZ(-90deg); 
  font-weight: bold;
  color: #fff;
}
/*菜单展开*/
.el-submenu.is-opened>.el-submenu__title .el-submenu__icon-arrow{
  -webkit-transform: rotateZ(0deg); 
  -ms-transform: rotate(0deg);
  transform: rotateZ(0deg); 
}
.el-menu-item.is-active {
  background-color: #2F7998 !important;
}
</style>


